import {api} from '/js/api';

api({

});



let swiper = new Swiper('.swiper-container', {
	slidesPerView: 'auto',
	centeredSlides: true,
	loop: true,
	loopFillGroupWithBlank: true,
	spaceBetween: 30,
});

let tmpl = template.compile(document.getElementById('tpl-result').innerHTML);
$('#like').click(function () {
	let mask = $(`<div class="mask"></div>`);
	mask.append(tmpl({result:50}));
	mask.on('click' , function () {
		mask.remove();
	});
	$('body').append(mask);
});